import React from 'react';
import {
  Box,
  Flex,
  Link,
  Button,
  Heading,
  Menu,
  MenuButton,
  MenuList,
  MenuItem,
  Image,
} from '@chakra-ui/react';
import { Link as RouterLink, useNavigate } from 'react-router-dom';
import { ChevronDownIcon } from '@chakra-ui/icons';

const logo = '/logo2.jpg';

const Layout: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const navigate = useNavigate();

  const handleLogout = () => {
    localStorage.removeItem('token');
    navigate('/');
  };

  return (
    <Box>
      <Flex
        as="nav"
        align="center"
        justify="space-between"
        wrap="wrap"
        padding="1rem 2rem"
        bgGradient="linear(to-r, blue.500, purple.600)"
        color="white"
        boxShadow="md"
      >
        <Flex align="center" mr={5}>
          <Image src={logo} alt="Dhanvikas Logo" boxSize="50px" mr={3} borderRadius="full" />
          <Heading as="h1" size="lg" letterSpacing={'-.1rem'}>
            Dhanvikas
          </Heading>
        </Flex>

        <Box>
          <Link as={RouterLink} to="/dashboard" mr={4} _hover={{ color: 'teal.200' }}>
            Dashboard
          </Link>
          <Link as={RouterLink} to="/expense-tracker" mr={4} _hover={{ color: 'teal.200' }}>
            Expense Tracker
          </Link>
          <Link as={RouterLink} to="/specialized-trackers" mr={4} _hover={{ color: 'teal.200' }}>
            Specialized Trackers
          </Link>
          <Link as={RouterLink} to="/monthly-report" mr={4} _hover={{ color: 'teal.200' }}>
            Monthly Report
          </Link>
          <Link as={RouterLink} to="/stocks" mr={4} _hover={{ color: 'teal.200' }}>
            Stocks
          </Link>
          <Link as={RouterLink} to="/goals" mr={4} _hover={{ color: 'teal.200' }}>
            Goals
          </Link>
          <Link as={RouterLink} to="/notifications" mr={4} _hover={{ color: 'teal.200' }}>
            Notifications
          </Link>
          <Menu>
            <MenuButton as={Button} rightIcon={<ChevronDownIcon />} bg="white" color="teal.500">
              Profile
            </MenuButton>
            <MenuList bg="white">
              <MenuItem as={RouterLink} to="/profile" color="teal.700">
                View Profile
              </MenuItem>
              <MenuItem onClick={handleLogout} color="teal.700">
                Logout
              </MenuItem>
            </MenuList>
          </Menu>
        </Box>
      </Flex>

      <Box p={4}>{children}</Box>
    </Box>
  );
};

export default Layout;