import React, { useState } from 'react';
import axios from 'axios';
import 'bootstrap/dist/css/bootstrap.min.css'; // Import Bootstrap CSS

const App = () => {
  const [stocks, setStocks] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  // Function to fetch stocks data from backend
  const fetchStocksData = async () => {
    setLoading(true); // Set loading state to true when request starts
    setError(null); // Reset previous error

    try {
      const response = await axios.get('http://localhost:5000/api/stocks');
      setStocks(response.data); // Update state with fetched data
      setLoading(false); // Set loading state to false when request completes
    } catch (err) {
      console.error('Error fetching stock data:', err);
      setError('Error fetching stock data'); // Display error message
      setLoading(false); // Set loading state to false on error
    }
  };

  return (
    <div className="container mt-4">
      <h1 className="text-center">Top 10 Stocks with Best Returns</h1>

      {/* Update Stocks button */}
      <div className="text-center mb-4">
        <button 
          onClick={fetchStocksData} 
          className="btn btn-primary" 
          disabled={loading}
        >
          {loading ? 'Updating...' : 'Update Stocks'}
        </button>
      </div>

      {/* Display error message if there is an error */}
      {error && <div className="alert alert-danger text-center">{error}</div>}

      {/* Display stocks in card format */}
      <div className="row">
        {stocks.map((stock) => (
          <div key={stock.symbol} className="col-md-3 mb-4">
            <div className="card">
              <div className="card-body">
                <h5 className="card-title">{stock.symbol}</h5>
                <p className="card-text">
                  {/* Show change percentage, format to 2 decimal places */}
                  Change: <strong>{stock.change === 'N/A' ? 'N/A' : stock.change.toFixed(2)}%</strong>
                </p>
                {/* Display more data */}
                <p className="card-text">
                  Price: <strong>${stock.price}</strong>
                </p>
                <p className="card-text">
                  High: <strong>${stock.high}</strong> | Low: <strong>${stock.low}</strong>
                </p>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default App;
